<template>
  <section>
    <div class="container mt-5">
      <h1 class="text-start mb-5"> </h1>

      <!--   -->
      <div class="input-group search-bar-container mb-3">
        <input type="text" v-model="searchQuery" class="form-control" placeholder="  ">
        <button class="btn btn-primary" @click="performSearch"></button>
      </div>

      <!--     -->
      <div class="d-flex justify-content-end mb-3">
        <label for="itemsPerPage" class="me-2">   :</label>
        <select id="itemsPerPage" v-model="itemsPerPage" @change="changeItemsPerPage" class="form-select w-auto">
          <option value="10">10</option>
          <option value="30">30</option>
          <option value="50">50</option>
          <option value="100">100</option>
        </select>
      </div>

      <!--    -->
      <table class="table custom-table table-hover">
        <thead>
          <tr>
            <th scope="col"></th>
            <th scope="col"></th>
            <th scope="col"></th>
            <th scope="col"></th>
            <th scope="col"></th>
            <th scope="col"></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="seminar in paginatedSeminars" :key="seminar.id" @click="goToDetail(seminar.id)">
            <td>{{ seminar.title }}</td>
            <td>{{ seminar.date }}</td>
            <td>{{ seminar.presentationDate }}</td>
            <td>{{ seminar.location }}</td>
            <td>{{ seminar.presenter }}</td>
            <td>{{ seminar.registrar }}</td>
          </tr>
        </tbody>
      </table>

      <!--      -->
      <div v-if="(isSearching && filteredSeminars.length === 0)">
        <p>  .    .</p>
      </div>

      <!--      -->
      <div v-if="isLoading">
        <p> ...   .</p>
      </div>

      <!--  -->
      <nav v-if="totalPages > 1" aria-label="Page navigation">
        <ul class="pagination justify-content-center">
          <li class="page-item" :class="{ disabled: currentPage === 1 }">
            <button class="page-link" @click="changePage(1)"></button>
          </li>
          <li class="page-item" :class="{ disabled: currentPage === 1 }">
            <button class="page-link" @click="changePage(currentPage - 1)"></button>
          </li>
          <li class="page-item" v-for="page in visiblePages" :key="page" :class="{ active: page === currentPage }">
            <button class="page-link" @click="changePage(page)">{{ page }}</button>
          </li>
          <li class="page-item" :class="{ disabled: currentPage === totalPages }">
            <button class="page-link" @click="changePage(currentPage + 1)"></button>
          </li>
          <li class="page-item" :class="{ disabled: currentPage === totalPages }">
            <button class="page-link" @click="changePage(totalPages)"></button>
          </li>
        </ul>
      </nav>

      <!--     -->
      <button @click="handleCreateSeminar" class="btn btn-success mt-3">  </button>
    </div>
  </section>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: "SeminarArchive",
  data() {
    return {
      searchQuery: '', //   v-model 
      seminars: [], // DB   
      filteredSeminars: [], //   
      isLoading: true, //  
      isSearching: false, //    
      itemsPerPage: 10, //    
      currentPage: 1, //  
      maxVisiblePages: 10, //      
    };
  },
  computed: {
    ...mapGetters(['isLoggedIn']), //   
    //      
    paginatedSeminars() {
      const start = (this.currentPage - 1) * this.itemsPerPage;
      const end = start + this.itemsPerPage;
      return this.filteredSeminars.slice(start, end);
    },
    //    
    totalPages() {
      return Math.ceil(this.filteredSeminars.length / this.itemsPerPage);
    },
    //      
    visiblePages() {
      const half = Math.floor(this.maxVisiblePages / 2);
      let start = Math.max(1, this.currentPage - half);
      let end = Math.min(this.totalPages, start + this.maxVisiblePages - 1);

      if (end - start < this.maxVisiblePages - 1) {
        start = Math.max(1, end - this.maxVisiblePages + 1);
      }

      return Array.from({ length: end - start + 1 }, (_, i) => start + i);
    }
  },
  methods: {
    goToDetail(seminarId) {
      if (seminarId) {
        this.$router.push({ name: 'SeminarDetail', params: { id: seminarId } });
      }
    },
    async fetchSeminars() {
      try {
        const response = await fetch('/api/seminars');
        if (!response.ok) {
          throw new Error('Network response was not ok');
        }
        const data = await response.json();
        if (Array.isArray(data)) {
          this.seminars = data.reverse(); //   
          this.filteredSeminars = this.seminars;
        } else {
          this.seminars = [];
        }
      } catch (error) {
        console.error('Error fetching seminars:', error);
        this.seminars = [];
      } finally {
        this.isLoading = false;
      }
    },
    performSearch() {
      this.isSearching = true;
      if (this.searchQuery) {
        this.filteredSeminars = this.seminars.filter(seminar =>
          seminar.title.toLowerCase().includes(this.searchQuery.toLowerCase())
        );
      } else {
        this.filteredSeminars = this.seminars;
      }
      this.currentPage = 1; //     
    },
    changeItemsPerPage() {
      this.currentPage = 1; //       
    },
    changePage(page) {
      if (page >= 1 && page <= this.totalPages) {
        this.currentPage = page;
      }
    },
    handleCreateSeminar() {
      if (this.isLoggedIn) {
        //        
        this.$router.push({ name: 'CreateSeminar' });
      } else {
        //         
        alert(' .   .');
        this.$router.push({ name: 'Login' });
      }
    }
  },
  mounted() {
    this.fetchSeminars();
  }
};
</script>

<style scoped>
.custom-table {
  cursor: pointer;
}
</style>
