<template>
  <div class="container mt-5">
    <div class="card">
      <div class="card-header">
        <h2>{{ seminar.title }}</h2>
      </div>
      <div class="card-body">
        <ul class="list-group list-group-flush">
          <li class="list-group-item">
            <strong>:</strong> {{ seminar.presentationDate }}
          </li>
          <li class="list-group-item">
            <strong>:</strong> {{ seminar.location }}
          </li>
          <li class="list-group-item">
            <strong>:</strong> {{ seminar.presenter }}
          </li>
          <li class="list-group-item">
            <strong>:</strong> {{ seminar.registrar }}
          </li>
          <li class="list-group-item">
            <strong> :</strong> {{ seminar.date }}
          </li>
        </ul>

        <!--   -->
        <div class="d-flex justify-content-start mt-4">
          <!--    -->
          <router-link to="/seminar-archive" class="btn btn-primary me-2">
             
          </router-link>

          <!--   -->
          <button @click="handleEditSeminar" class="btn btn-warning ms-3">
            
          </button>

          <!--   -->
          <button @click="handleDeleteSeminar" class="btn btn-danger ms-3">
            
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: "SeminarDetail",
  data() {
    return {
      seminar: {}, //    
    };
  },
  computed: {
    ...mapGetters(['isLoggedIn']), // Vuex    
  },
  mounted() {
    //   ID   
    const seminarId = this.$route.params.id;
    this.fetchSeminarDetail(seminarId);
  },
  methods: {
    //    
    async fetchSeminarDetail(seminarId) {
      try {
        const response = await fetch(`/api/seminars/${seminarId}`);
        if (!response.ok) {
          throw new Error('Network response was not ok');
        }
        const data = await response.json();
        this.seminar = data; //  
      } catch (error) {
        console.error('Error fetching seminar detail:', error);
      }
    },

    //   
    async handleDeleteSeminar() {
      if (!this.isLoggedIn) {
        alert(' .   .');
        this.$router.push({ name: 'Login' });
        return;
      }

      const confirmed = confirm('   ?');
      if (!confirmed) return;

      try {
        const response = await fetch(`/api/seminars/${this.$route.params.id}`, {
          method: 'DELETE',
        });

        if (!response.ok) {
          throw new Error('Network response was not ok');
        }

        alert(' .');
        this.$router.push('/seminar-archive');
      } catch (error) {
        console.error('Error deleting seminar:', error);
        alert('  .');
      }
    },

    //   
    handleEditSeminar() {
      if (!this.isLoggedIn) {
        alert(' .   .');
        this.$router.push({ name: 'Login' });
        return;
      }

      //     
      this.$router.push({ name: 'EditSeminar', params: { id: this.seminar.id } });
    }
  }
};
</script>

