<template>
  <section>
    <div class="container mt-5">
      <!--         -->
      <h1 class="text-start mb-5">{{ isEditMode ? ' ' : '  ' }}</h1>

      <!--  /  -->
      <form @submit.prevent="submitSeminar">
        <div class="mb-3">
          <label for="title" class="form-label"> </label>
          <input type="text" id="title" v-model="seminar.title" class="form-control" required>
        </div>

        <div class="mb-3">
          <label for="date" class="form-label"> </label>
          <input type="date" id="date" v-model="seminar.date" class="form-control" required>
        </div>

        <div class="mb-3">
          <label for="presentationDate" class="form-label"></label>
          <input type="date" id="presentationDate" v-model="seminar.presentationDate" class="form-control" required>
        </div>

        <div class="mb-3">
          <label for="location" class="form-label"></label>
          <input type="text" id="location" v-model="seminar.location" class="form-control" required>
        </div>

        <div class="mb-3">
          <label for="presenter" class="form-label"></label>
          <input type="text" id="presenter" v-model="seminar.presenter" class="form-control" required>
        </div>

        <div class="mb-3">
          <label for="registrar" class="form-label"></label>
          <input type="text" id="registrar" v-model="seminar.registrar" class="form-control" required>
        </div>

        <button type="submit" class="btn btn-primary">{{ isEditMode ? ' ' : '' }}</button>
      </form>
    </div>
  </section>
</template>

<script>
export default {
  name: "CreateSeminar",
  data() {
    return {
      seminar: {
        title: '',
        date: '',
        presentationDate: '',
        location: '',
        presenter: '',
        registrar: ''
      },
      isEditMode: false //   
    };
  },
  methods: {
    async fetchSeminarDetails(id) {
      try {
        const response = await fetch(`/api/seminars/${id}`);
        if (!response.ok) {
          throw new Error('   .');
        }
        const data = await response.json();
        this.seminar = data; //      
      } catch (error) {
        console.error('Error fetching seminar details:', error);
      }
    },
    async submitSeminar() {
      try {
        const url = this.isEditMode ? `/api/seminars/${this.$route.params.id}` : '/api/seminars';
        const method = this.isEditMode ? 'PUT' : 'POST';

        const response = await fetch(url, {
          method: method,
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify(this.seminar)
        });

        if (!response.ok) {
          throw new Error('   .');
        }

        // /      
        this.$router.push('/seminar-archive');
      } catch (error) {
        console.error('Error submitting seminar:', error);
      }
    }
  },
  mounted() {
    const seminarId = this.$route.params.id;
    if (seminarId) {
      // seminarId     
      this.isEditMode = true;
      this.fetchSeminarDetails(seminarId); //       
    }
  }
};
</script>

