<template>
  <div class="container mt-5">
    <div class="card">
      <div class="card-header">
        <h2>{{ talent.name }} - {{ talent.position }}</h2>
      </div>
      <div class="card-body">
        <ul class="list-group list-group-flush">
          <li class="list-group-item">
            <strong>:</strong> {{ talent.birthYear }}
          </li>
          <li class="list-group-item">
            <strong>:</strong> {{ talent.phone }}
          </li>
          <li class="list-group-item">
            <strong>:</strong> {{ talent.email }}
          </li>
          <li class="list-group-item">
            <strong>:</strong> {{ talent.school }}
          </li>
          <li class="list-group-item">
            <strong>:</strong> {{ talent.experience }}
          </li>
          <li class="list-group-item">
            <strong> :</strong> {{ talent.expertise }}
          </li>
          <li class="list-group-item">
            <strong> :</strong> {{ talent.status }}
          </li>
          <li class="list-group-item">
            <strong> :</strong> {{ talent.source }}
          </li>
          <li class="list-group-item">
            <strong> :</strong> {{ talent.resumeNumber }}
          </li>
          <li class="list-group-item">
            <strong> :</strong> {{ talent.contactPerson }}
          </li>
          <li class="list-group-item">
            <strong> :</strong> {{ talent.jobDescription }}
          </li>
          <li class="list-group-item">
            <strong> :</strong> {{ talent.qualifications }}
          </li>
          <li class="list-group-item">
            <strong>   :</strong> {{ talent.majorAndCareer }}
          </li>
        </ul>

        <!--   -->
        <div class="d-flex justify-content-start mt-4">
          <!--    -->
          <router-link to="/talent-database" class="btn btn-primary me-2">
             
          </router-link>

          <!--   -->
          <button @click="handleEditTalent" class="btn btn-warning ms-3">
            
          </button>

          <!--   -->
          <button @click="handleDeleteTalent" class="btn btn-danger ms-3">
            
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: "TalentDetail",
  data() {
    return {
      talent: {}, //    
    };
  },
  computed: {
    ...mapGetters(['isLoggedIn']), // Vuex    
  },
  mounted() {
    //   ID   
    const talentId = this.$route.params.id;
    this.fetchTalentDetail(talentId);
  },
  methods: {
    //    
    async fetchTalentDetail(talentId) {
      try {
        const response = await fetch(`/api/talents/${talentId}`);
        if (!response.ok) {
          throw new Error('Network response was not ok');
        }
        const data = await response.json();
        this.talent = data; //  
      } catch (error) {
        console.error('Error fetching talent detail:', error);
      }
    },

    //   
    async handleDeleteTalent() {
      if (!this.isLoggedIn) {
        alert(' .   .');
        this.$router.push({ name: 'Login' });
        return;
      }

      const confirmed = confirm('   ?');
      if (!confirmed) return;

      try {
        const response = await fetch(`/api/talents/${this.$route.params.id}`, {
          method: 'DELETE',
        });

        if (!response.ok) {
          throw new Error('Network response was not ok');
        }

        alert(' .');
        this.$router.push('/talent-database');
      } catch (error) {
        console.error('Error deleting talent:', error);
        alert('  .');
      }
    },

    //   
    handleEditTalent() {
      if (!this.isLoggedIn) {
        alert(' .   .');
        this.$router.push({ name: 'Login' });
        return;
      }

      //     
      this.$router.push({ name: 'EditTalent', params: { id: this.talent.id } });
    }
  }
};
</script>

