<script>
import { mapActions, mapState } from 'vuex';

export default {
  name: "HeaderComponent",
  data() {
    return {
      logoUrl: '/logo-unionsearch.jpg'  //    
    };
  },
  computed: {
    ...mapState(['isLoggedIn', 'userName'])  // Vuex      
  },
  created() {
    this.checkLoginStatus();  //      
  },
  methods: {
    ...mapActions(['checkLoginStatus', 'logout']),  // Vuex  

    //  
    async handleLogout() {
      try {
        const result = await this.logout();  //   (Vuex     )
        if (result.success) {
          this.$router.push('/');  //     
        } else {
          console.error(' :', result.message);
        }
      } catch (error) {
        console.error('   :', error);
      }
    }
  }
};
</script>

<template>
  <nav class="navbar navbar-expand-lg navbar-dark" style="background-color: #092a49;">
    <router-link to="/" class="navbar-brand company-name">
      
      <img :src="logoUrl" alt="Company Logo" class="company-logo" />
    </router-link>
    <div class="nav-right-items d-flex">
      <!--    -->
      <div class="nav-item dropdown">
        <a class="nav-link dropdown-toggle" href="#" id="companyDropdown" role="button" data-bs-toggle="dropdown"
          aria-expanded="false">
           
        </a>
        <ul class="dropdown-menu" aria-labelledby="companyDropdown">
          <li><router-link to="/ceo-greeting" class="dropdown-item"> </router-link></li>
          <li><router-link to="/organization-chart" class="dropdown-item"></router-link></li>
          <li><router-link to="/history" class="dropdown-item"></router-link></li>
          <li><router-link to="/location" class="dropdown-item"> </router-link></li>
          <li><router-link to="/contact" class="dropdown-item"></router-link></li>
        </ul>
      </div>

      <!--    -->
      <router-link to="/consultant" class="nav-item nav-link"></router-link>
      <router-link to="/open-position" class="nav-item nav-link"> </router-link>
      <router-link to="/talent-database" class="nav-item nav-link"> DB</router-link>
      <router-link to="/seminar-archive" class="nav-item nav-link"> </router-link>

      <!--      -->
      <div v-if="isLoggedIn" class="nav-item dropdown">
        <a class="nav-link dropdown-toggle" href="#" id="userDropdown" role="button" data-bs-toggle="dropdown"
          aria-expanded="false">
          {{ userName }} <!--     -->
        </a>
        <ul class="dropdown-menu" aria-labelledby="userDropdown">
          <li>
            <button @click="handleLogout" class="dropdown-item"></button> <!--   -->
          </li>
        </ul>
      </div>
      <router-link v-else to="/login" class="nav-item nav-link"></router-link> <!--     -->
    </div>
  </nav>
</template>

<style scoped>
.company-logo {
  height: 50px;
}
</style>
