<template>
  <section>
    <div class="container mt-5">
      <h1 class="text-start mb-5"> DB</h1>

      <!--   -->
      <div class="input-group search-bar-container mb-3">
        <input type="text" v-model="searchQuery" class="form-control" placeholder=" ">
        <button class="btn btn-primary" @click="performSearch"></button>
      </div>

      <!--     -->
      <div class="d-flex justify-content-end mb-3">
        <label for="itemsPerPage" class="me-2">   :</label>
        <select id="itemsPerPage" v-model="itemsPerPage" @change="changeItemsPerPage" class="form-select w-auto">
          <option value="10">10</option>
          <option value="30">30</option>
          <option value="50">50</option>
          <option value="100">100</option>
        </select>
      </div>

      <!--    -->
      <table class="table custom-table table-hover">
        <thead>
          <tr>
            <th scope="col"></th>
            <th scope="col"></th>
            <th scope="col"></th>
            <th scope="col"></th>
            <th scope="col"></th>
            <th scope="col"></th>
            <!--<th scope="col"></th>-->
            <th scope="col"></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="talent in paginatedTalents" :key="talent.id" @click="goToDetail(talent.id)">
            <td>{{ formatDate(talent.date) }}</td>
            <td>{{ talent.position }}</td>
            <td>{{ talent.name }}</td>
            <td>{{ talent.phone }}</td>
            <td>{{ talent.email }}</td>
            <td>{{ talent.school }}</td>
            <!--<td>{{ talent.status }}</td>-->
            <td>{{ talent.contactPerson }}</td>
          </tr>
        </tbody>
      </table>

      <!--  -->
      <nav v-if="totalPages > 1" aria-label="Page navigation">
        <ul class="pagination justify-content-center">
          <li class="page-item" :class="{ disabled: currentPage === 1 }">
            <button class="page-link" @click="changePage(1)"></button>
          </li>
          <li class="page-item" :class="{ disabled: currentPage === 1 }">
            <button class="page-link" @click="changePage(currentPage - 1)"></button>
          </li>
          <li class="page-item" v-for="page in visiblePages" :key="page" :class="{ active: page === currentPage }">
            <button class="page-link" @click="changePage(page)">{{ page }}</button>
          </li>
          <li class="page-item" :class="{ disabled: currentPage === totalPages }">
            <button class="page-link" @click="changePage(currentPage + 1)"></button>
          </li>
          <li class="page-item" :class="{ disabled: currentPage === totalPages }">
            <button class="page-link" @click="changePage(totalPages)"></button>
          </li>
        </ul>
      </nav>

      <!--      -->
      <div v-if="(isSearching && filteredTalents.length === 0)">
        <p>  .    .</p>
      </div>

      <!--      -->
      <div v-if="isLoading">
        <p> ...   .</p>
      </div>

      <!--     -->
      <button @click="handleCreateTalent" class="btn btn-success mt-3">  </button>
    </div>
  </section>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: "TalentArchive",
  data() {
    return {
      searchQuery: '',  // 
      talents: [],  // DB   
      filteredTalents: [], //   
      isLoading: true,  //  
      currentPage: 1,   //  
      itemsPerPage: 10, //    
      maxVisiblePages: 10, //    
      isSearching: false //  
    };
  },
  computed: {
    ...mapGetters(['isLoggedIn']), //  
    //      
    paginatedTalents() {
      const start = (this.currentPage - 1) * this.itemsPerPage;
      const end = start + this.itemsPerPage;
      return this.filteredTalents.slice(start, end);
    },
    //    
    totalPages() {
      return Math.ceil(this.filteredTalents.length / this.itemsPerPage);
    },
    //      
    visiblePages() {
      const half = Math.floor(this.maxVisiblePages / 2);
      let start = Math.max(1, this.currentPage - half);
      let end = Math.min(this.totalPages, start + this.maxVisiblePages - 1);

      if (end - start < this.maxVisiblePages - 1) {
        start = Math.max(1, end - this.maxVisiblePages + 1);
      }

      return Array.from({ length: end - start + 1 }, (_, i) => start + i);
    }
  },
  methods: {
    goToDetail(talentId) {
      this.$router.push({ name: 'TalentDetail', params: { id: talentId } });
    },
    async fetchTalents() {
      this.isLoading = true;
      try {
        const response = await fetch('/api/talents');
        if (!response.ok) {
          throw new Error('Network response was not ok');
        }
        const data = await response.json();
        if (Array.isArray(data)) {
          this.talents = data.reverse(); //   
          this.filteredTalents = this.talents;
        } else {
          this.talents = [];
        }
      } catch (error) {
        console.error('Error fetching talents:', error);
        this.talents = [];
      } finally {
        this.isLoading = false; //     
      }
    },
    performSearch() {
      this.isSearching = true;
      if (this.searchQuery) {
        // , ,    
        this.filteredTalents = this.talents.filter(talent =>
          talent.name.toLowerCase().includes(this.searchQuery.toLowerCase()) ||
          talent.position.toLowerCase().includes(this.searchQuery.toLowerCase()) ||
          talent.school.toLowerCase().includes(this.searchQuery.toLowerCase())
        );
      } else {
        this.filteredTalents = this.talents;
      }
      this.currentPage = 1; //     
    },
    changeItemsPerPage() {
      this.currentPage = 1; //       
    },
    changePage(page) {
      if (page >= 1 && page <= this.totalPages) {
        this.currentPage = page;
      }
    },
    handleCreateTalent() {
      if (this.isLoggedIn) {
        this.$router.push({ name: 'CreateTalent' });
      } else {
        alert(' .   .');
        this.$router.push({ name: 'Login' });
      }
    },
    formatDate(date) {
      return new Date(date).toLocaleDateString(); //  
    }
  },
  mounted() {
    this.fetchTalents();
  }
};
</script>

<style scoped>
.custom-table {
  cursor: pointer;
}
</style>
