<template>
  <section>
    <div class="container mt-5">
      <!--         -->
      <h1 class="text-start mb-5">{{ isEditMode ? ' ' : '  ' }}</h1>

      <!--  /  -->
      <form @submit.prevent="submitPosition">
        <div class="mb-3">
          <label for="title" class="form-label"></label>
          <input type="text" id="title" v-model="position.title" class="form-control" required>
        </div>

        <div class="mb-3">
          <label for="company" class="form-label"></label>
          <input type="text" id="company" v-model="position.company" class="form-control" required>
        </div>

        <div class="mb-3">
          <label for="level" class="form-label"></label>
          <input type="text" id="level" v-model="position.level" class="form-control" required>
        </div>

        <div class="mb-3">
          <label for="status" class="form-label"></label>
          <input type="text" id="status" v-model="position.status" class="form-control" required>
        </div>

        <div class="mb-3">
          <label for="date" class="form-label"></label>
          <input type="date" id="date" v-model="position.date" class="form-control" required>
        </div>

        <div class="mb-3">
          <label for="description" class="form-label"></label>
          <textarea id="description" v-model="position.description" class="form-control" rows="5" required></textarea>
        </div>

        <button type="submit" class="btn btn-primary">{{ isEditMode ? ' ' : '' }}</button>
      </form>
    </div>
  </section>
</template>

<script>
export default {
  name: "CreatePosition",
  data() {
    return {
      position: {
        title: '',
        company: '',
        level: '',
        status: '',
        date: '',
        description: ''
      },
      isEditMode: false //   
    };
  },
  methods: {
    async fetchPositionDetails(id) {
      try {
        const response = await fetch(`/api/positions/${id}`);
        if (!response.ok) {
          throw new Error('   .');
        }
        const data = await response.json();
        this.position = data; //      
      } catch (error) {
        console.error('Error fetching position details:', error);
      }
    },
    async submitPosition() {
      try {
        const url = this.isEditMode ? `/api/positions/${this.$route.params.id}` : '/api/positions';
        const method = this.isEditMode ? 'PUT' : 'POST';

        const response = await fetch(url, {
          method: method,
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify(this.position)
        });

        if (!response.ok) {
          throw new Error('   .');
        }

        //  /      
        this.$router.push('/open-position');
      } catch (error) {
        console.error('Error submitting position:', error);
      }
    }
  },
  mounted() {
    const positionId = this.$route.params.id;
    if (positionId) {
      // positionId     
      this.isEditMode = true;
      this.fetchPositionDetails(positionId); //       
    }
  }
};
</script>

